const BASE_URL = "https://api.themoviedb.org/4";
const HEADERS = { "Content-Type": "application/json" };

export async function createRequestToken() {
  const res = await fetch(`${BASE_URL}/auth/request_token`, {
    method: "POST",
    headers: HEADERS,
  })
  const data = await res.json()
  return data.request_token
}

export async function createAccessToken(request_token) {
  const res = await fetch(`${BASE_URL}/auth/access_token`, {
    method: "POST",
    headers: HEADERS,
    body: JSON.stringify({ request_token }),
  })
  const data = await res.json()
  return data.access_token
}

export async function getUserInfo(accessToken) {
  const res = await fetch(`${BASE_URL}/account`, {
    headers: {
      Authorization: `Bearer ${accessToken}`,
      "Content-Type": "application/json",
    },
  })
  return res.json()
}

export async function logout(accessToken) {
  const res = await fetch(`${BASE_URL}/auth/access_token`, {
    method: "DELETE",
    headers: HEADERS,
    body: JSON.stringify({ access_token: accessToken }),
  })
  return res.ok
}

